* {
  margin: 0;
  box-sizing: border-box;
}

#react-logo {
  width: 80px;
  cursor: pointer;
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 10px;
  background-color: #21222a;
  min-height: 15vh;
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
}

ul {
  list-style: none;
}

.logo-react {
  display: flex;
  align-items: center;
  color: #00d8ff;
  font-size: 1.5rem;
}

.main-container {
  min-height: 85vh;
  padding-top: 80px;
  background-color: #282d35;
  color: white;
  text-align: left;
  padding-left: 100px;
  background-image: url(./logo512.png);
  background-repeat: no-repeat;
  background-position-x: right;
  background-position-y: center;
}

#main-heading {
  padding-left: 10px;
  padding-bottom: 50px;
}

.facts-list {
  line-height: 2;
  font-size: large;
}

.main-container > main > ul {
  list-style-type: disc;
}

.main-container > main > ul > li::marker {
  color: #00d8ff;
  font-size: 1.4rem;
}
/* media queries for small devices */

@media only screen and (max-width: 768px) {
  .main-container {
    margin-left: 0;
    padding-left: 0;
  }
}
